per // then you'll be sorry, but then it will be too late)</p>\n<p>An old adolescent punk song from a German band that still hasn't grown up. Christopher is already breaking hearts, but hopefully he can skip the star bit.</p>\n<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2001/03/ball.jpg\" height=\"332\" width=\"249\" border=\"0\" alt=\"ball.jpg: \" /></p>"},"next":{"fields":{"title":"Kissed the Air, Said Goodbye...","date":"07 Mar 2001","slug":"/2001-03-07-kissed-the-air-said-goodbye/"}},"previous":{"fields":{"title":"kill 1","date":"12 Mar 2001","slug":"/2001-03-12-kill-1/"}}}],"pathPrefix":"","first":false,"last":false,"index":1015,"pageCount":1101}}

